body {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif,  -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.jumbotron {
  background-image: url(/personalPage/static/media/beach.7bec86f3.jpg);
  height: 500px;
}

.navbar {
  right: 130px;
  font-size: 25px;
  font-weight: 500;
  font-family: 'Pacifico', cursive;
}

.nav-link {
  margin: 5px;
}

.banner {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Pacifico', cursive;
  margin: 15px;
}
.photo {
  content: url(/personalPage/static/media/picture.d2a2c259.jpg);
  display: block;
  margin: auto;
  object-fit: cover;
  height: 180px;
  width: 180px;
  border-radius: 50%;
  border: white solid 5px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 200px;
}

li a {
  display: block;
  color: #000;
  text-decoration: none;
}

li a:hover {
  color: white;
}
.footer {
  position: fixed;
  bottom: 0;
}
.watermark {
  position: fixed;
  right: 10px;
}
.container {
  font-family: 'Merriweather', serif, 'Noto Serif TC', serif;

}
.headText {
  -webkit-writing-mode: vertical-rl;
          writing-mode: vertical-rl;
  text-orientation: upright;
}

.info {
  font-size: 25px;
}

.table td {
  height: auto;
}
.headText {
  -webkit-writing-mode: vertical-rl;
          writing-mode: vertical-rl;
  text-orientation: upright;
}
.card {
  padding: 30px;
}

.card-title {
  line-height: 3px;
  padding-bottom: 10px;
  background-color: #a2c4c8;
}

.card-text {
  font-size:20px;
}

.card img {
  display: block;
  object-fit: cover;
  height: 180px;
  width: 200px;
  opacity: 0.9;
}

.card .Basketball {
  content: url(/personalPage/static/media/basketball.1258df4a.jpg);
}

.card .Music {
  content: url(/personalPage/static/media/guitar.74bcfbe0.jpg);
}

.card .Workout {
  content: url(/personalPage/static/media/workout.332df7eb.JPG);
}
